import React from "react";
import { cva } from "class-variance-authority";

interface ButtonProps {
  customClass?: any;
  name: string;
  onClickMethod: any;
  type?: "button";
  color?: string;
}

export default function Button({
  name,
  onClickMethod,
  type = "button",
  color = 'default',
  customClass
}: ButtonProps) {

  const finalStyles = cva(['custom-lib-button'], {
    variants: {
      color: {
        gray: [ 'custom-lib-grey-button' ],
        default: [ 'custom-lib-default-button' ]
      },
    }
  });

  return (
    <button
      type={type}
      className={`${finalStyles({ color } as Object)}${customClass ? ` ${customClass}` : ''}`}
      onClick={() => onClickMethod()}
    >
      {name}
    </button>
  );
}
